import React from 'react';

type Props = {
  goods: Good[]
};

export const GoodsList: React.FC<Props> = ({ goods }) => {
  return (
    <div>
      <ul>
        {goods.map(good => (
          <li
            key={good.id}
            style={{ color: `${good.color}` }}
          >
            {good.name}
          </li>
        ))}
      </ul>
    </div>
  );
};
